.adapter_submitter, .adapter_routing, .adapter_reciver{
	background-color: #5995f7;
	color: white;
}

.bp-object {
	min-height: 60px;
	position: fixed;
	min-width: 100px;
}

.bp-object-name {
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-pack: center;
	    justify-content: center;
	text-align: center;
	height: 60px;
}

.bp-objects-container {
	position: absolute;
}

.footer {
	border: 1px solid #5995f7;
	padding: 10px 0;
	text-align: center;
}

.passed {
	background-color: green;
	color: white;
}

.not-passed {
	background-color: white;
	color: #5995f7;
}

.error {
	background-color: #9e2b0e;
	color: white;
}

.gumcid-input {
  border-radius: 2px;
  padding: 8px;
  font-size: 18px;
  margin: 1%;
  width: 30%;
}

.queue {
	background-color: #eae24d;
	color: #5995f7;
}

.system {
	border-radius: 8px;
	background-color: #e2831e;
	color: white;
}

.svg-container {
    height: 100%;
    left: 0;
    position: fixed;
    width: 100%;
}


